import Vue from 'vue'
import App from './App.vue'
import Vuetify from 'vuetify' // path to vuetify export
import 'vuetify/dist/vuetify.min.css'

Vue.config.productionTip = false
Vue.use(Vuetify)

new Vue({
  vuetify: new Vuetify({
    theme: { dark: true },
  }) ,
  render: function (h) { return h(App) },
}).$mount('#app')
