<template>
  <div id="app">
    <v-app>
    <v-main id="login-progress" >
      <div style="width:240px;  margin:auto; text-align: center">
        <div class="text-h5 text--primary" style="margin-bottom:40px">
          {{ nickname }}
        </div>
        <v-progress-linear :color="progress_color" v-model="progress" rounded class="my-6"></v-progress-linear>
        <div class="text--secondary mb-1">{{ login_phase }} </div>
        <small class="text--secondary" style="text-transform: capitalize;">{{ workspace_mode }}
          Mode</small>
      </div>
    </v-main>
  </v-app>
  </div>
</template>

<script>
const params =  new URL(window.location).searchParams
import "@/assets/scss/style.scss"

export default {
  data: (vm) => ({
    progress: 10,
    progress_color: "grey",
    login_phase: "Authenticating",
    nickname: params.get("nickname"),
    workspace_mode: params.get("workspace_mode")
  }),
  methods: {
    UpdateProgress(description) {
      switch (description) {
        case "Unlocking":
          this.login_phase = "Bypass Charlock"
          this.progress = 20
          break;
        case "Loading":
          this.login_phase = "Loading into game"
          this.progress += 5
          break
        case "Loaded":
          this.login_phase = "Flash Loaded, Displaying Game"
          this.progress = 100
          this.progress_color = "success"
          break
        case "conflict":
          this.login_phase = "Failed to load, the character already online somewhere"
          this.progress = 100
          this.progress_color = "red"
          break
      }
    }
  },
  mounted(){
    window.$com = this
  }
}
</script>
